
export default {
	globalData: {
		userInfo: null,
		host: "http://127.0.0.1:8000/",
		apiVersion: "v1.0/"
	},
	onLaunch: function() {
		console.log('App Launch')	
		var that = this
		console.log(that)
		that.$options.getUserInfo()
	},
	// 如果用户授权，获取用户信息
	getUserInfo(){
		var that = this
		uni.getSetting({
			success(res) {
				console.log(res.authSetting['scope.userInfo'])
				if(res.authSetting['scope.userInfo']){
					uni.getUserInfo({
						success(e) {
							console.log(that)
							that.globalData.userInfo = e.userInfo
						}
					})
				}
			}
		})
	},
	onShow: function() {
		console.log('App Show')
	},
	onHide: function() {
		console.log('App Hide')
	}
}
